<template>
<div class="chart-view">
    <van-nav-bar title="数据分析" left-text="返回" left-arrow @click-left="$router.go(-1)" />
    <div class="empty-box" v-if="chartData.length === 0">
		<van-empty
			class="custom-image"
			image="https://img.yzcdn.cn/vant/custom-empty-image.png"
			description="没有数据哦，去喝一杯吧~"
		>
			<button class="btn" @click="$router.push({name: 'Menu'})">去喝一杯 ~</button>
		</van-empty>
	</div>
	<canvas ref="canvas" />
	<div class="item-box" v-if="chartData.length > 0">
		<h3 class="item-title">购买统计：</h3>
		<ul>
			<li class="item" v-for="(item,index) in chartData" :key="index">
				<span class="item-color" :style="{backgroundColor: item.color}" />
				<span class="item-name">{{item.name}}</span>
				<span class="item-count">x{{item.count}}</span>
			</li>
		</ul>
	</div>
</div>
</template>

<script>
import {
    mapGetters
} from 'vuex'

export default {
    data() {
        return {
			chartData: [],
			canvas: '',
			ctx: '',
            sAngle: 0, // 开始的角度
            eAngle: 0 // 结束的角度
        }
    },
    mounted() {
		// 获取canvas元素
		this.canvas = this.$refs.canvas;
		// 获取渲染上下文
        this.ctx = this.canvas.getContext('2d');
		// 移动端 放大canvas宽高，解决边缘锯齿
		let width = window.innerWidth
		let height = 300;
		if(window.devicePixelRatio) {
			this.canvas.style.width = width + 'px'
			this.canvas.style.height = height + 'px'
			this.canvas.width = width * window.devicePixelRatio
			this.canvas.height = height * window.devicePixelRatio
			this.ctx.scale(window.devicePixelRatio, window.devicePixelRatio)
		}
		this.getOrderData(0)
		
    },
    methods: {
        // 制作饼形图
        arc(item) {
            // PC端 放大canvas宽高，解决边缘锯齿
            // let width = canvas.width
            // let height = canvas.height
            // canvas.style.width = width + 'px'
            // canvas.style.height = height + 'px'
            // canvas.width = width * 2
            // canvas.height = height * 2
			// ctx.scale(2, 2)

            // 获取结束的角度  item.size * 360 为每个项目进度的度数 + 上一个项目进度角度
            this.eAngle = (item.size * 360) + this.sAngle;

            console.log('每个项目的进度度数', item.name + '==>' + this.eAngle);

			// 临时变量，用来动画时计数
			let count = 0;
			
			// promise函数里的this指向不正确，需要先获取
			let _this = this;

            return new Promise(function (resolve) {
                let timer = setInterval(() => {
                    // 启动定时器，每次+1，
                    // 比如一开始是this.sAngle是0,eAgnle是120度
                    // 当count + 到this.eAngle值时，就退出
                    // 每一次退出都要上eAgnle赋值给this.sAngle
                    count++
                    if ((count + _this.sAngle) > _this.eAngle) {
                        clearInterval(timer);
                        // 制作文字
                        // 斜边
                        let c = 85;
                        // 角度的一半
                        let d = (_this.eAngle + _this.sAngle) / 2;
                        console.log(d);
                        // 对边
                        let y = 150 + Math.sin(d * Math.PI / 180) * c
                        // 邻边
                        let x = 140 + Math.cos(d * Math.PI / 180) * c
                        _this.ctx.beginPath()
                        _this.ctx.font = '14px'
                        _this.ctx.textALign = 'left'
                        _this.ctx.fillStyle = item.color
                        _this.ctx.fillText(item.name, x, y)
                        _this.ctx.closePath()
                        _this.sAngle = _this.eAngle;
                        resolve('完成11')
                        return;
                    }

                    _this.ctx.save()
                    _this.ctx.beginPath()
                    _this.ctx.moveTo(170, 150)

                    _this.ctx.fillStyle = item.color
                    _this.ctx.strokeStyle = item.color

                    _this.ctx.arc(170, 150, 50, _this.sAngle * Math.PI / 180, (count + _this.sAngle) * Math.PI / 180, false)
                    _this.ctx.lineTo(170, 150)
                    _this.ctx.fill()
                    _this.ctx.stroke()
                    _this.ctx.closePath()
                    _this.ctx.restore()

                }, 1);
            })
		},
		// 异步等待函数
        async asyncArc() {
			console.log(this.chartData);
			for (let i = 0; i < this.chartData.length; i++) {
				await this.arc(this.chartData[i]);
			}

            // const p1 = await arc(data[0]);
            // const p2 = await arc(data[1]);
            // const p3 = await arc(data[2]);
            // const p4 = await arc(data[3]);

        },
        // 获取订单数据
        getOrderData(status) {

            if (!this.getToken) {
                this.$router.push({
                    name: 'Login'
                })

                return;
            }

            let params = {
                appkey: this.appkey,
                tokenString: this.getToken,
                status // 状态为0时获取所有数据
            }

            console.log(params);

            this.$toast({
                type: 'loading',
                message: '加载中',
                forbidClick: true,
                loadingType: 'spinner',
                duration: 0
            });

            this.axios({
                    method: 'GET',
                    url: '/findOrder',
                    params
                })
                .then(res => {
                    this.orderList = [];
                    this.$toast.clear();
                    console.log(" 数据分析", res);
                    // 数据格式
                    //  [
                    //  	{
                    //  		name: '抹茶瑞纳冰',
                    //  		size: 0.5,
                    //  		color: 'red'
                    //  	}
                    //  ]
                    if (res.data.code === 70000) {
                        // let arr = [];
                        let obj = {};
						let total = 0;
						
                        // 统计购买的商品个数 以及 所有购买数量
                        res.data.result.forEach(item => {
							// 随机取色
							let color1 = Math.floor(Math.random() * (255 - 1 + 1) + 1);
							let color2 = Math.floor(Math.random() * (255 - 1 + 1) + 1);
							let color3 = Math.floor(Math.random() * (255 - 1 + 1) + 1);
                            if (!obj[item.pid]) {
                                obj[item.pid] = {
                                    name: item.name,
                                    color: `rgb(${color1},${color2},${color3})`,
                                    count: 1
                                }
                                total += 1;
                            } else {
                                obj[item.pid].count++;
                                total += 1;
                            }
                        })

                        // 将每件商品分成total份得出占比，再乘10  再除以10得到小数

                        for (let k in obj) {
                            obj[k].size = (10 * (obj[k].count / total)) / 10
                            this.chartData.push(obj[k])
                        }
						console.log(this.chartData);

						setTimeout(() => {
							this.asyncArc();
						},200)
                    }
                })
                .catch(err => {
                    this.$toast.clear();
                    console.log(err);

                })

        }
    },
    computed: {
		...mapGetters(['getToken'])
    }
}
</script>

<style lang="scss" scoped>
.chart-view {

    /deep/ .van-nav-bar .van-icon,
    /deep/ .van-nav-bar__text {
        color: #323233;
	}
	
	.item-box {
		padding: 0 20px;

		.item-title {
			margin-bottom: 10px;
			font-size: 14px;
		}

		.item {
			margin-bottom: 3px;

			.item-color {
				display: inline-block;
				margin-right: 3px;
				border-radius: 3px;
				width: 30px;
				height: 14px;
			}

			.item-name {
				display: inline-block;
				margin-right: 3px;
				vertical-align: top;
				font-size: 12px;
				color: #323233;
			}
			
			.item-count {
				display: inline-block;
				vertical-align: top;
			}
		}

		
	}

	.empty-box {

		/deep/ .custom-image .van-empty__image {
			width: 90px;
			height: 90px;
		}
	
		/deep/ .van-empty__description {
			margin-top: 28px;
		}

		.btn {
			border: 1px solid #9a9a9a;
			border-radius: 16px;
			padding: 12px 36px;
			font-size: 12px;
			color: #9a9a9a;
			background-color: #fff;
		}
	}
}
</style>
